* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: #fff;
}

.App-Main {
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
  top: 80px;
  background-color: #bdd4f1;
  height: 100%;
  min-height: 541px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
}


.nav {
  background-color: #10203f;
  z-index: 50;
  position: fixed;
  width: 100%;
  padding: 10px 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: space-around;
  box-shadow: 0px 2px 6px #436ba2;
}

.titulo {
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  text-shadow: 2px 2px 0px #80aded;
}

.titulo:hover {
  color: #80aded;
  text-decoration: none;
  text-shadow: 2px 2px 0px #fff;
  transition: .2s ease-in-out;
}

.btnsCategoriasContainer {
  width: 40vw;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.btnCategorias {
  color: #93b9ed;
  text-decoration: none;
  border-bottom: 1px solid transparent;

}

.btnCategorias:hover {
  color: #fff;
  border-bottom: 1px solid #fff;
  transition: .2s ease-in-out;
}

.iconoCarrito {
  color: #93b9ed;
  text-decoration: none;

}

.iconoCarrito:hover {
  color: #fff;
  transition: .2s ease-in-out;
  cursor: pointer;
}

.logo {
  width: 60px;
}

.cartContainer {
  display: flex;
  flex-direction: row;
}

.numberCart {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 4px;
  color: #00ff2a;
  font-size: 15px;
  border: 1px solid #00ff2a;
  border-radius: 50%;
  width: 20px;
  height: 18px;
  padding-bottom: 2px;
}

.itemCountContainer {
  height: 90px;
  width: 130px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.countBox {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.btnMinus {
  color: #1d4173;
  background-color: transparent;
  border: none;
  outline-style: none;
  border: 2px solid #1d4173;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fa-minus {
  width: 13px;
  height: 11.7px;
}

.btnMinus:hover {
  border: 2px solid #bb2121;
  color: #bb2121;
  cursor: pointer;
}

.btnMinus:active {
  border: 2px solid #8e1818;
  color: #8e1818;
}

.btnMinus:disabled {
  border: 2px solid #8e1818;
  color: #8e1818;
  cursor: default;
}

.btnPlus {
  color: #1d4173;
  background-color: transparent;
  border: none;
  outline-style: none;
  border: 2px solid #1d4173;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.fa-plus {
  width: 13px;
  height: 11.7px;
}

.btnPlus:hover {
  border: 2px solid #36b14b;
  color: #36b14b;
}

.btnPlus:active {
  border: 2px solid #026813;
  color: #026813;
}

.btnPlus:disabled {
  border: 2px solid #026813;
  color: #026813;
  cursor: default;
}



.countNumber {
  color: #10203f;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 25px;
  outline: none;
  border: 1px solid #1d4173;
  border-radius: 5px;
}

.btnAddToCart {
  text-decoration: none;
  width: 120px;
  height: 30px;
  padding: 3px 6px;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  background: transparent;
  color: #80aded;
  border: 1px solid #80aded;
}

.btnAddToCart:hover {
  color: #93b9ed;
  border: 1px solid #93b9ed;
}

.btnAddToCart:active {
  color: #1d4173;
  border: 1px solid #1d4173;
}

.btnAddToCart:disabled {
  color: #1d4173;
  border: 1px solid #1d4173;
  cursor: default;
}

.stockText {
  color: #1d4173;
  font-size: 12px;
}

.itemContainer {
  width: 100%;
  max-width: 300px;
  min-width: 235px;
  height: 420px;
  margin: 10px;
  background-color: #fff;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border: 1px solid #fff;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 1px #436ba2;
}

.itemContainer:hover {
  box-shadow: 0px 20px 35px 0px #2f568d;
  z-index: 1;
  transition: .2s ease-in-out;
}

.itemImg {
  width: 180px;
  border: 1px solid transparent;
  border-radius: 5px;
}

.itemTitle {
  color: #1d4173;
}

.itemPrice {
  color: #1d4173;
}

.itemCard {
  display: flex;
  min-height: 220px;
  max-height: 220px;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.itemDetailcard {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.itemDetailBox {
  display: flex;
  flex-direction: row;
}

.itemDetailImg {
  width: 199px;
  border: 1px solid transparent;
  border-radius: 7px;
}

.itemDetailTag {
  font-size: 25px;
  color: #1d4173;
  text-shadow: 2px 2px 2px #2f568d;
}

.itemDetailPrice {
  color: #1d4173;
}

.itemDetailDescription {
  width: 600px;
  font-size: 25px;
  color: #1d4173;
  padding: 5px;
}

.btnDetalle {
  padding: 3px 6px 6px 6px;
  text-decoration: none;
  width: auto;
  height: auto;
  padding: 3px 6px 6px 6px;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  background: transparent;
  color: #80aded;
  border: 1px solid #80aded;
}

.btnDetalle:active {
  color: #10203f;
  border: 1px solid #10203f;
}

.btnDetalle:hover {
  color: #1d4173;
  border: 1px solid #1d4173;
}

.sectionMain {
  display: flex;
  width: 100%;
  max-width: 1350px;
  height: auto;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.cardDetail {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  background: #fff;
  height: 340px;
  width: 290px;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 1px #436ba2;
}

.cardDetail:hover {
  box-shadow: 0px 20px 35px 0px #2f568d;
  z-index: 1;
  transition: .2s ease-in-out;
}

.categoryName {
  font-size: 25px;
  color: #10203f;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  text-shadow: 1px 1px 5px #1d4173;
}

.btnCartWidget {
  text-decoration: none;
}

.btnGoToCart {
  display: flex;
  text-decoration: none;
  width: 120px;
  height: 50px;
  padding: 3px 6px;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  background: transparent;
  color: #80aded;
  border: 1px solid #80aded;
  align-items: center;
  justify-content: center;
}

.btnGoToCart:hover {
  color: #436ba2;
  border: 1px solid #436ba2;
}

.btnGoToCart:active {
  color: #1d4173;
  border: 1px solid #1d4173;
}

.btnGoToCart:disabled {
  color: #1d4173;
  border: 1px solid #1d4173;
  cursor: default;
}

.itemCardCart {
  display: flex;
  width: 250px;
  min-height: 266px;
  max-height: 293px;
  height: 100%;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
}

.cartBox {
  border: 2px solid #436ba2;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  align-self: flex-start;
}

.cartTicketBox {
  border: 2px solid #436ba2;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 15px;
  align-self: flex-start;
  align-items: center;
}

.cardCartContainer {
  display: flex;
  width: 95%;
  flex-direction: column;
  height: auto;
  align-items: center;
  justify-content: center;
}

.btnCartClear {
  width: 100px;
  background: transparent;
  border: 2px solid #436ba2;
  color: #436ba2;
  border-radius: 5px;
  padding: 4px 2px;
  margin-bottom: 10px;
  align-self: flex-end;
}

.btnCartClear:hover {
  color: #10203f;
  border: 2px solid #10203f;
  cursor: pointer;
}

.btnItemClear {
  width: 80px;
  height: auto;
  background: transparent;
  border: 2px solid #436ba2;
  color: #436ba2;
  border-radius: 5px;
  padding: 4px 2px;
}

.btnItemClear:hover {
  color: #10203f;
  border: 2px solid #10203f;
  cursor: pointer;
}

.btnItemBuy {
  width: 135px;
  height: 35px;
  background: transparent;
  border: 2px solid #436ba2;
  color: #436ba2;
  border-radius: 5px;
  padding: 4px 2px;
}

.btnItemBuy:hover {
  color: #10203f;
  border: 2px solid #10203f;
  cursor: pointer;
}

.btnItemBuy:disabled {
  color: #090b0e;
  border: 2px solid #090b0e;
  cursor: pointer;
}

.btnsItemContainer {
  display: flex;
  flex-direction: column;
  height: 100px;
  width: 122px;
  align-items: center;
  justify-content: space-around;
}

.hiperVinculo {
  align-self: end;
}

.link {
  text-decoration: none;
}

.numberCartDisabled {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 4px;
  color: transparent;
  font-size: 15px;
  border: 1px solid transparent;
  border-radius: 50%;
  width: 20px;
  height: 18px;
  padding-bottom: 2px;
}

.spinner {
  position: relative;
  top: 250px;
  align-self: center;
  border: 4px solid #fff;
  border-left-color: #2557fb;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.buyBox {
  width: 190px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: space-around;
  justify-content: space-around;
}

.saldoBox {
  display: flex;
  flex-direction: column;
  height: 70px;
  justify-content: space-around;
}

.priceText {
  color: #1d4173;
  font-size: 18px;
}

.totalText {
  color: #1d4173;
  font-size: 16px;
  font-weight: bolder;
  border-bottom: 1px solid #1d4173;
}

.tituloCart {
  color: #80aded;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  text-shadow: 2px 2px 0px #2f568d;
}

.btnGoToHome {
  position: relative;
  top: 50px;
  width: 100px;
  background: transparent;
  border: 2px solid #436ba2;
  color: #436ba2;
  border-radius: 5px;
  padding: 4px 2px;
  margin-bottom: 10px;
  align-self: flex-end;
}

.btnGoToHome:hover {
  color: #10203f;
  border: 2px solid #10203f;
  cursor: pointer;
}

.totalBox {
  top: 30%;
  left: 60%;
  width: 250px;
  z-index: 25;
  position: fixed;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border: 2px solid #436ba2;
  border-radius: 5px;
}

.ticketContainer {
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
  top: 55%;
  left: 60%;
  width: 250px;
  display: flex;
  border: 2px solid #436ba2;
  border-radius: 7px;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 270px;
}

.footer {
  top: 35px;
  align-self: flex-end;
  position: relative;
  height: 60px;
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: #10203f;
  box-shadow: 0px 2px 6px #436ba2;
  align-items: center;
  justify-content: space-around;
}

.footerText {
  color: #fff;
  font-size: 18px;
}
